.title,.bold-link,.project__visit-link{font-family:"Inter","Helvetica Neue",Helvetica,Arial,"PingFang TC","黑體-繁","Heiti TC","蘋果儷中黑","Apple LiGothic Medium","微軟正黑體","Microsoft JhengHei",sans-serif;font-weight:700;text-transform:uppercase;letter-spacing:.05em}.bold-link,.project__visit-link{color:#e0e2db;text-decoration:none;transition:color .2s}.bold-link:focus-visible,.project__visit-link:focus-visible{color:#ffe865;outline:2px solid #ffe865}.bold-link:hover,.project__visit-link:hover{color:#ffe865}h1,h2,h3,h4,p{mix-blend-mode:exclusion}.projects__list{padding-left:0;list-style-type:none;display:grid;grid-template-columns:repeat(auto-fit, minmax(315px, 1fr));grid-gap:1rem}.projects__item-link{display:flex;flex-direction:column;justify-content:center;box-sizing:border-box;width:100%;padding:.5rem;color:#e0e2db;text-decoration:none;font-weight:700;font-size:1.5rem;background-color:#191716;transition:color .2s}.projects__item-link:focus-visible{color:#ffe865;outline:2px solid #ffe865}.projects__item-link:hover{color:#ffe865}.projects__item-image{margin:0;width:100%;height:auto}.projects__item-name{margin-bottom:1rem;font-size:1.5rem}.projects__item-name::after{transform:translateZ(0);transition:width .15s ease-out;display:block;content:"";margin-top:1rem;width:0%;height:2px;background-color:#ffe865}.projects__item:hover .projects__item-name::after{width:100%}.projects__item-description{font-family:"Lato","Helvetica Neue",Helvetica,Arial,"PingFang TC","黑體-繁","Heiti TC","蘋果儷中黑","Apple LiGothic Medium","微軟正黑體","Microsoft JhengHei",sans-serif;text-transform:none;font-weight:normal;margin-top:1rem;font-size:1rem}.project__info{margin-top:0}.project__header{margin-top:4.5rem}.project__hero{width:100%;height:min(56vw,50vh);background-position:top;background-size:cover;display:flex;align-items:center;justify-content:center;animation:headerFade 1s;background-repeat:no-repeat}.project__logo{max-width:95vw;max-height:80%}.project__title{display:flex;flex-wrap:wrap;align-items:center;margin-top:1rem;margin-bottom:1rem}.project__name{font-family:"Inter","Helvetica Neue",Helvetica,Arial,"PingFang TC","黑體-繁","Heiti TC","蘋果儷中黑","Apple LiGothic Medium","微軟正黑體","Microsoft JhengHei",sans-serif;border-right:3px solid #e0e2db;margin-top:1rem;margin-bottom:1rem;margin-right:1rem;padding-right:1rem;letter-spacing:.05em;text-transform:uppercase}.project__visit-link{white-space:nowrap;border:3px solid #e0e2db;border-radius:5px;padding:.5rem 1rem;transition:background-color .1s ease;text-shadow:none;mix-blend-mode:exclusion}.project__visit-link:hover{color:#191716;background:#e0e2db;padding:.5rem 1rem}.project_hr{border:none;height:2px;background:#e0e2db;margin-left:0;width:100%;animation:expandWidth 1s;mix-blend-mode:exclusion}.project__description{margin:3rem 0}.project__btn-return{width:3rem;height:3rem;background-color:#191716;border:4px solid #e0e2db;position:fixed;top:6.5rem;left:2rem;display:flex;align-items:center;justify-content:center;font-size:1.5rem;z-index:99;transition:border-color .2s}.project__btn-return:hover,.project__btn-return:focus{border:4px solid #ffe865}.project__screenshots{padding-left:0;list-style-type:none;display:grid;grid-template-columns:1fr;gap:2rem}.project__screenshots-img{width:100%}.project__tags{display:flex;flex-wrap:wrap;padding-left:0;list-style-type:none}.project__tag{display:block;padding:.25rem;margin-right:.5rem;mix-blend-mode:exclusion;white-space:nowrap}.project__tag-label{display:inline-block;height:.5rem;width:.5rem;margin-right:.5rem;border-radius:50%}@keyframes headerFade{from{opacity:0}to{opacity:1}}@keyframes expandWidth{from{width:0}to{width:100%}}@media(max-width: 768px){.projects__list{display:flex;flex-wrap:wrap}.projects__item{width:100%}.project__screenshots{display:flex;flex-wrap:wrap}}