.title,.bold-link,.nav__link,.nav__link--active{font-family:"Inter","Helvetica Neue",Helvetica,Arial,"PingFang TC","黑體-繁","Heiti TC","蘋果儷中黑","Apple LiGothic Medium","微軟正黑體","Microsoft JhengHei",sans-serif;font-weight:700;text-transform:uppercase;letter-spacing:.05em}.bold-link,.nav__link,.nav__link--active{color:#e0e2db;text-decoration:none;transition:color .2s}.bold-link:focus-visible,.nav__link:focus-visible,.nav__link--active:focus-visible{color:#ffe865;outline:2px solid #ffe865}.bold-link:hover,.nav__link:hover,.nav__link--active:hover{color:#ffe865}h1,h2,h3,h4,p{mix-blend-mode:exclusion}.nav-wrapper{position:fixed;top:0;left:0;right:0;background-color:#191716;z-index:999}.nav{display:flex;margin:10px;margin-bottom:0;padding:0;border:4px solid #e0e2db;border-bottom:4px dashed #e0e2db;justify-content:flex-start;align-items:center;height:4.5rem}.nav__brand{margin-left:2rem;width:50px;height:50px}.nav__links{margin:0;margin-left:2rem;padding-left:.8rem;list-style-type:none;border-left:4px solid #e0e2db;display:flex;flex-wrap:wrap}.nav__link,.nav__link--active{display:block;font-size:1.5rem;padding:.5rem .8rem}.nav__link{color:#8a8d83}.nav__link::after,.nav__link--active::after{transition:width .15s ease-out;display:block;content:"";margin-top:1px;width:0%;height:2px;background-color:#ffe865}.nav__link:hover::after,.nav__link--active:hover::after{width:100%}.nav__menu-toggle{font-size:1.5rem;padding:.5rem 0;margin-right:2rem;color:#e0e2db;background-color:#191716;border:none;cursor:pointer;float:right;display:none;height:50px}.nav__menu-toggle:hover{color:#ffe865}@media(max-width: 768px){.nav{display:block;min-height:4.5rem;height:auto}.nav__menu-toggle,.nav__brand{margin-top:calc((4.5rem - 54px)/2)}.nav__menu-toggle{display:block}.nav__links{display:none;margin:1rem 2rem}.nav__links.active{display:block}}